.checkout{
    display: -ms-flexbox;
    display: flex;
    padding: 20px;
    background-color: white;
    height: -webkit-max-content;
    height: -moz-max-content;
    height: max-content;
}
.checkout_ad{
    width: 100%;
    margin-bottom: 10px;
}
.checkout_title{
    margin-right: 10px;
    padding: 10px;
    border-bottom: 1px solid lightgray; 
}
.checkout_name{
    margin-right: 10px;
    padding: 10px;
}


