.home{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    margin-left: auto;
    margin-right: auto;
    max-width: 1500px;
}
.home_row{
    z-index: 1;
    margin-left: 5px;
    margin-right: 5px;
    display: -ms-flexbox;
    display: flex;
}
.home_image{
    width: 100%;
    z-index: -1;        /*So that this will be behind everything else*/
    margin-bottom: -170px;
    -webkit-mask-image: -webkit-gradient(linear, left top, left bottom, from(rgba(0,0,0,1)), to(rgba(0,0,0,0)));
    -webkit-mask-image: -webkit-linear-gradient(top, rgba(0,0,0,1), rgba(0,0,0,0));
            mask-image: -webkit-gradient(linear, left top, left bottom, from(rgba(0,0,0,1)), to(rgba(0,0,0,0)));
            mask-image: linear-gradient(to bottom, rgba(0,0,0,1), rgba(0,0,0,0)); /*This will give the image a diminishing/fade in bottom part*/
}




